.best-clothes {
  @include grid();
  @include media-lg(up) {
    background: url("/img/cloth-min.jpg") no-repeat right 76px; }
  padding: 64px 0;
  .wrapper {
    width: 580px;
    max-width: 100%; }
  &__caption {
    color: #2a2a2a;
    font-size: 48px;
    font-weight: 900;
    position: relative;
    border-left: 15px solid #5fcad4;
    line-height: 60px;
    padding: 10px 0 10px 32px;
    margin-bottom: 64px;
    width: 100%;
    @include media-md(down) {
      font-size: 32px;
      line-height: 36px; }
    @include media-sm(down) {
      font-size: 28px; } }
  &__item {
    margin-bottom: 48px;
    display: table;
    width: 100%;
    > div {
      display: table-cell;
      vertical-align: middle; } }
  .text {
    font-size: 20px;
    font-weight: 500;
    padding-left: 48px;
    @include media-md(down) {
      padding-left: 24px; } }
  &__ps {
    margin-top: 48px;
    font-weight: 300;
    color: #7d7d7d;
    @include media-md(down) {
      margin-top: 24px; } } }
