// Popup notice block
.popup-notice {
  box-shadow: 0px 0px 24px 0px rgba(0, 0, 2, 0.09);
  padding: 20px;
  position: absolute;
  background-color: #fcd82f;
  display: flex;
  border-radius: 2px;
  @include align(between, center);
  transform: scale(0);
  z-index: 5;
  transition: transform .3s ease-in-out;
  &.active {
    transform: scale(1); }
  i {
    margin-right: 20px; }
  &__text {
    font-size: 12px;
    color: #010101; }
  &__triangle {
    width: 26px;
    position: absolute;
    color: #fcd82f;
    font-size: 28px;
    bottom: -21px;
    right: 25px;
    text-shadow: 0 5px 24px rgba(0, 0, 2, 0.09); }
  &_wishlist {
    min-width: 180px;
    right: -10px;
    top: -95px;
    .popup-notice__text {
      width: 98px;
      text-align: left; } }
  &_wishlist-header {
    display: block;
    padding: 15px 20px;
    top: -58px;
    right: -10px;
    .popup-notice__triangle {
      font-size: 18px;
      bottom: -15px; } }
  &_basket {
    top: -45px;
    right: -10px;
    .popup-notice__triangle {
      font-size: 18px;
      bottom: -15px; } }
  &_size {
    width: 80%;
    display: block;
    text-align: center;
    top: -132px;
    left: 10%;
    i {
      margin-right: 0; }
    .popup-notice__text {
      width: 98px;
      margin: 10px auto;
      text-align: center; }
    .popup-notice__triangle {
      left: calc(50% - 13px); } } }
#tooltip {
  pointer-events: none;
  position: absolute;
  max-width: 90%;
  left: 10000px;
  top: 0px;
  z-index: 1000;
  display: none;
  font-size: 12px;
  background-color: #fff;
  border: 1px solid #000;
  padding: 2px 3px; }
