.gift-sertificate {
  margin-top: 20px;
  @include grid(fluid);
  .btn {
    @include col(18);
    @include media-sm(down) {
      @include col(24);
      margin-left: auto;
      margin-right: auto; } }
  .img-wrapper {
    width: 100%;
    margin-bottom: 20px;
    @include media-md(up) {
      @include col(12); }
    img {
      width: 100%; } }
  .wrapper {
    p:first-child {
      padding-top: 0;
      margin-top: 0; }
    @include media-md(up) {
      @include col(11); } } }
