// News, articles
.article-detailed {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  &__image {
    @include media-sm(down) {
      @include col(24); }
    @include media-md(up) {
      @include col(8); } }

  &__text {
    @include media-sm(down) {
      @include col(24); }
    @include media-md(up) {
      @include col(16); }
    & > p {
      &:first-child {
        margin-top: 0; } } }
  &__share {
    margin-top: 20px; } }

//recipe items
.recipe-item {
  color: #000;
  text-decoration: none;
  width: 272px;
  padding-bottom: 20px;
  @include media-lg(up) {
    @include col(6); }
  @include media-md() {
    @include col(8); }
  @include media-sm(down) {
    @include col(12); }
  &:hover {
    .img {
      transform: rotate(-2deg) scale(1.05); } }

  &__wrapper {
    float: right;
    .caption {
      padding-top: 0; } }
  .caption {
    font-weight: 900;
    font-size: 18px;
    line-height: 20px;
    margin: 24px 0 4px; }
  .img-wrapper {
    position: relative;
    padding-left: 100%;
    padding-top: 100%;
    overflow: hidden; }
  .img {
    @include absolute-center;
    background: no-repeat center center;
    background-size: cover;
    transition: transform 0.3s ease 0.3s; }
  .text {
    margin-top: 20px;
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    p {
      font-weight: 500;
      padding: 0;
      margin: 0; } } }
.recipe-item_text {
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  p {
    font-weight: 500;
    padding: 0;
    margin: 0; } }
