.contacts {
  h1 {
    margin-bottom: 0; }
  &__mt {
    margin-top: 30px; }
  &__phones {
    margin-top: 30px;
    @include media-sm(down) {
      @include col(24); }
    @include media-md() {
      @include col(17); }
    @include media-lg() {
      @include col(17); }
    @include media-xl() {
      @include col(10); } }
  &__time {
    margin-top: 30px;
    @include media-sm(down) {
      @include col(24); }
    @include media-md(up) {
      @include col(7); } }
  &__address {
    margin-top: 30px;
    @include media-sm(down) {
      @include col(24); }
    @include media-lg(down) {
      @include col(24); }
    @include media-xl() {
      @include col(7); } }
  &__text {
    margin-top: 30px;
    line-height: 24px;
    font-size: 14px;
    & > strong {
      font-weight: 700;
      font-size: 16px; }
    &_phones {
      & > * {
        vertical-align: middle;
        @include media-sm(up) {
          margin-left: 20px; }
        @include media-xs() {
          &img {
            margin-left: 5px; } } }
      & > br {
        @include hidden-sm(up); }
      & > strong > a {
        text-decoration: none;
        color: #272727; } } }
  &__note-1 {
    text-align: center;
    div {
      margin: 30px auto;
      display: inline-block;
      @include media-sm(down) {
        @include col(24); }
      @include media-md(up) {
        @include col(12); } }
    @include media-sm(down) {
      @include col(24); }
    @include media-md(up) {
      @include col(24); } }
  &__note-2 {
    @include media-sm(down) {
      @include col(24); }
    @include media-md(up) {
      @include col(7); } }
  &__get-by {
    margin-top: 30px;
    @include col(24);
    @include media-sm(up) {
      display: flex; }
    & > div {
      &:first-child {
        min-width: 96px;
        @include hidden-xs(); } } }
  &__gallery {
    margin-top: 30px;
    @include col(24);
    & > div > * {
      margin-top: 12px;
      margin-bottom: 12px;
      @include media-xs() {
        @include col(24); }
      @include media-sm() {
        @include col(12); }
      @include media-md(up) {
        @include col(6); }
      & > img {
        min-width: 100%;
        max-width: 100%; } } }
  &__distributors {
    & > div {
      margin-top: 0; }
    @include align(start,center); }
  &__map {
    @include hidden-xs();
    margin-top: 12px;
    @include col(24);
    & > img {
      max-width: 100%; } } }
