footer {
  background-color: #292929;
  padding: 60px 0 50px;
  position: relative;
  @include media-md(down) {
    padding-top: 40px; }
  .line {
    @include hidden-sm(down);
    border-top: 1px solid #474747;
    position: absolute;
    bottom: 164px;
    width: 100%;
    left: 0; } }
.footer {
  width: 100%;
  &__address {
    a {
      font-size: inherit;
      color: inherit;
      text-decoration: none; } }
  &__info {
    padding-top: 50px;
    font-size: 14px;
    color: #afafaf;
    text-align: center; } }
.navigation-footer {
  @include grid-in();
  @include align(between);
  margin-bottom: 40px;
  ul {
    padding: 0; }
  a {
    color: #ffffff;
    text-decoration: none;
    font-size: 14px;
    line-height: 30px;
    transition: color .3s ease;
    &:hover, &:focus {
      color: #f6c339; } }
  &__column {
    @include media-md(down) {
      @include col(24); }
    @include media-lg(up) {
      @include col(4); }
    &:last-child {
      @include media-sm(down) {
        text-align: center; }
      @include media-md() {
        display: flex;
        @include align(between); }
      @include media-lg() {
        @include col(5); } }

    @include media-md(down) {
      &_dropdown {
        border-bottom: 1px solid #474747;
        & > ul {
          overflow: hidden;
          max-height: 0;
          margin: 0;
          transition: max-height .3s ease, margin .3s ease;
          & > li > a {
            display: block;
            height: 100%; } }
        .navigation-footer__title {
          display: flex;
          @include align(between, center);
          cursor: pointer; } } } }
  &__title {
    font-size: 18px;
    font-weight: 900;
    color: #ffffff;
    text-transform: uppercase;
    @include media-md(down) {
      margin: 0;
      padding: 20px 0; }
    @include media-lg(up) {
      margin-top: 0;
      margin-bottom: 30px; }
    &_mt {
      margin-top: 50px; }
    i {
      @include hidden-lg(up);
      transition: transform .3s ease; }
    &.active {
      i {
        transform: rotate(180deg); }
      & + ul {
        max-height: 400px;
        margin: 16px 0; } } }
  &__phone {
    @include media-sm(down) {
      text-align: center;
      & > div {
        display: inline-block; }
      & > * {
        vertical-align: top; } }
    @include media-md(up) {
      display: flex; }
    margin-bottom: 20px;

    a {
      font-size: 18px;
      font-weight: 700;
      line-height: .8; }
    span {
      color: #ffffff;
      font-size: 14px;
      font-weight: 300; } }
  &__social {
    a:not(:first-child) {
      margin-left: 35px; } } }

// Footer map
.map {
  height: 400px;
  width: 100%;
  @include hidden-sm(down);
  background: url(/img/footer-map-min.jpg) no-repeat top center; }
