// Main slider
.main-slider {
  position: relative;
  @include media-md(down) {
    margin-top: 24px; }
  @include hidden-xs();
  &__wrap {
    margin: 0 auto;
    max-width: 1920px;
    overflow: hidden; }
  &__track {
    display: flex;
    &:not(.js-dragged) {
      transition: transform .3s ease; } }
  &__banner {
    max-width: 100%;
    display: block;
    &_md-up {
      @include hidden-sm(down); }
    &_sm {
      @include hidden-md(up); } }
  & > .btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    width: 68px;
    height: 68px;
    padding: 0;
    transform: translateY(-50%);
    &.left {
      left: 5%; }
    &.right {
      right: 5%;
      transform: translateY(-50%) rotate(180deg); } } }

// Carousel pagination
.carousel-pagination {
  position: absolute;
  bottom: 5%;
  width: 100%;
  text-align: center;
  &__page {
    vertical-align: middle;
    display: inline-block;
    width: 13px;
    height: 13px;
    border: 2px solid #ffffff;
    border-radius: 50%;
    margin: 5px 9px;
    cursor: pointer;
    transition: background-color .2s ease, margin .2s ease, width .2s ease, height .2s ease;
    &.active {
      width: 23px;
      height: 23px;
      margin: 0 4px;
      border-width: 7px;
      background-color: #00ad5d; } } }

// Colored background
.colored-bg {
  &_f3f3f3 {
    background-color: #f3f3f3; } }
// Main benefits
