        @include col(24); } }
    &:nth-child(2) {
      @include media-lg(up) {
        width: calc(30% - 48px); }
      @include media-md() {
        @include col(8); }
      @include media-sm() {
        @include col(12); }
      @include media-xs() {
        @include col(24); } }
    &:nth-child(3) {
      @include media-lg(up) {
        width: calc(30% - 48px); }
      @include media-md() {
        @include col(8); }
      @include media-sm() {
        @include col(12); }
      @include media-xs() {
        @include col(24); } }
    &:nth-child(4) {
      @include media-lg(up) {
        width: calc(40% - 48px); }
      @include media-md() {
        @include col(16); }
      @include media-sm(down) {
        @include col(24); } }
    &:last-child {
      @include media-lg(up) {
        width: calc(60% - 48px); }
      @include media-md(down) {
        @include col(24); } } } }


.recipe-article-preview {
  position: relative;
  color: #ffffff;
  text-shadow: 1px 1px rgba(0,0,0,.3);
  &__image {
    & > img {
      min-width: 100%;
      display: block;
      transition: transform 1s .1s; }
    &:after {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.3);
      width: 0;
      height: 0;
      border-radius: 50%;
      opacity: 0;
      transform: scale(.2);
      transition: transform .6s .1s, opacity .6s .1s, width .6s .1s, height .6s .1s; }
    &:hover, &:focus, &:active {
      & > img {
        transform: rotate(-2deg) scale(1.05); }
      &:after {
        width: 100%;
        height: 100%;
        opacity: 1;
        transform: scale(2); } } }
  &__caption {
    position: absolute;
    bottom: 20px;
    left: 20px; }
  &__title {
    font-size: 24px;
    font-weight: 900; }
  &__preview-text {
    margin-top: 20px;
    font-size: 16px; } }


