// Cart page with products
.product-cart {
  min-width: 100%; }
.product-cart-header {
  margin-top: 30px;
  @include media-sm(up) {
    display: flex;
    @include align(between, center); }
  &__title {
    display: flex; }
  &__h1 {
    margin: 0; }
  &__count {
    font-size: 12px;
    margin-left: 20px;
    & > i {
      font-style: normal;
      padding: 5px;
      text-align: center; } }
  & > .btn_back-link {
    @include hidden-xs(); } }


// Cart products table
.product-cart-table {
  margin-top: 30px;
  &__header {
    font-size: 12px;
    color: #9f9f9f;
    @include media-xs() {
      & > div {
        &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6) {
          @include hidden-xs(); } } } }
  &__row {
    padding-top: 10px;
    @include align(start, center);
    &:after {
      margin-top: 10px;
      @include col(24);
      content: "";
      width: 100%;
      border-bottom: 1px solid #d6d6d6; } }
  &__col {
    @include media-xs() {
      padding-top: 20px; }
    &_photo {
      @include media-xs() {
        @include col(9); }
      @include media-sm(up) {
        @include col(4); }
      @include media-lg(up) {
        @include col(3); } }
    &_name {
      @include media-xs() {
        @include col(15); }
      @include media-sm() {
        @include col(7); }
      @include media-md(up) {
        @include col(6); } }
    &_quantity {
      @include media-md(up) {
        text-align: center; }
      @include media-xs() {
        @include col(9); }
      @include media-sm(up) {
        @include col(5); } }
    &_price {
      @include hidden-sm(down);
      @include media-sm() {
        @include col(4); }
      @include media-md() {
        @include col(3); }
      @include media-lg(up) {
        @include offset(right, 1);
        @include col(3); } }
    &_price-sum {
      @include media-xs() {
        @include col(15); }
      @include media-sm() {
        @include col(5); }
      @include media-md() {
        @include col(4); }
      @include media-lg(up) {
        @include offset(right, 1);
        @include col(3); } }
    &_remove {
      text-align: right;
      @include col(2); } }
  &__product {
    position: relative; }
  &__image {
    display: inline-block;
    position: relative;
    //width: 100%
    //height: 122px
    border: 1px solid #ebebeb;
    & > img {
      display: block;
      max-width: 100%;
 } }      //+image-center()
  &__name {
    text-decoration: none;
    color: #303030;
    font-size: 14px;
    & > span {
      color: #a6a6a6; } }
  &__size {
    margin-top: 20px;
    font-size: 14px;
    font-weight: 700;
    & > span {
      margin-left: 10px;
      font-weight: 500;
      color: #a6a6a6; } }
  &__price {
    font-size: 16px;
    font-weight: 700; }
  &__price-sum {
    @include media-xs() {
      font-size: 24px; }
    @include media-sm() {
      font-size: 20px; }
    @include media-md(up) {
      font-size: 24px; }
    line-height: 1;
    font-weight: 700; }
  &__remove {
    position: absolute;
    top: -1px;
    right: 12px;
    display: inline-block;
    cursor: pointer;
    width: 30px;
    height: 30px;
    border: 1px solid #d6d6d6;
    text-align: center;
    line-height: 30px;
    transition: background-color .3s ease;
    &.icon-fade > i.active {
      top: 8px;
      left: 8px; }
    &:hover, &:focus {
      background-color: #f5f5f5; } }
  &__separator {
    width: 1px;
    height: 80px;
    background-color: #ebebeb;
    @include hidden-xs();
    &_md-up {
      @include hidden-sm(down); } } }

// Products quantity input
.quantity {
  display: inline-flex;
  @include align(start, center);
  position: relative;
  &__input {
    @include media-md(down) {
      width: 40px; }
    @include media-lg(up) {
      width: 85px; }
    padding: 5px;
    border: 1px solid #c2c2c2;
    text-align: center;
    font-size: 14px;
    line-height: 18px; }
  &__handle {
    cursor: pointer;
    &.icon-fade {
      & > i {
        &.active {
          top: -3px;
          left: -5px; } } }
    &_minus {
      margin-right: 15px;
      margin-top: 3px; }
    &_plus {
      margin-left: 15px;
      margin-top: -2px;
      transform: rotate(180deg); } } }

// Cart footer
.product-cart-footer {
  margin-top: 30px;
  @include align(start, center);
  &__checkout {
    @include media-xs() {
      @include col(24); }
    @include media-sm() {
      @include col(12); }
    @include media-md() {
      @include col(8); }
    @include media-lg(up) {
      @include col(5); }
    font-size: 18px;
    font-weight: 700; }
  &__quick-buy {
    @include media-xs() {
      @include col(24);
      margin-top: 20px; }
    @include media-sm() {
      @include col(12); }
    @include media-md() {
      @include col(8); }
    @include media-lg(up) {
      @include col(5); }
    font-size: 18px;
    font-weight: 700; }
  &__back-link {
    @include media-sm(down) {
      margin-top: 30px;
      @include col(24);
      text-align: center; }
    @include media-md() {
      text-align: right;
      @include col(8); }
    @include media-lg(up) {
      text-align: left;
      @include col(5); } }
  &__cart-total {
    @include media-md(down) {
      @include col(24);
      text-align: right;
      order: -1;
      margin-bottom: 30px; }
    @include media-lg(up) {
      @include col(9); }
    font-size: 24px;
    font-weight: 300;
    color: #010101; }
  &__price-total {
    display: inline-block;
    font-weight: 700;
    font-size: 30px;
    color: #000000;
    margin-left: 20px; } }
