@mixin hidden($direction: only) {
  @if $direction == only or $direction == up {
    display: none; }
  @else if $direction == down {
    display: none; } }

@mixin hidden-xl($direction: only) {
  @if $direction == only or $direction == up {
    @include media-xl(up) {
      display: none; } }
  @else if $direction == down {
    @include media-xl(down) {
      display: none; } } }

@mixin hidden-lg($direction: only) {
  @if $direction == only {
    @media screen and (min-width: $lg) and (max-width: $xl) {
      display: none; } }
  @else if $direction == down {
    @include media-lg(down) {
      display: none; } }
  @else if $direction == up {
    @include media-lg(up) {
      display: none; } } }

@mixin hidden-md($direction: only) {
  @if $direction == only {
    @media screen and (min-width: $md) and (max-width: $lg) {
      display: none; } }
  @else if $direction == down {
    @include media-md(down) {
      display: none; } }
  @else if $direction == up {
    @include media-md(up) {
      display: none; } } }

@mixin hidden-sm($direction: only) {
  @if $direction == only {
    @media screen and (min-width: $sm) and (max-width: $md) {
      display: none; } }
  @else if $direction == down {
    @include media-sm(down) {
      display: none; } }
  @else if $direction == up {
    @include media-sm(up) {
      display: none; } } }

@mixin hidden-xs($direction: down) {
  @if $direction == down {
    @include media-xs(down) {
      display: none; } } }
