@mixin media-xl($direction: only) {
  @if ($direction == up or $direction == only) {
    @media screen and (min-width: $xl) {
      @content; } }
  @else if ($direction == down) {
    @media screen and (max-width: $xl - 1) {
      @content; } } }

@mixin media-lg($direction: only) {
  @if ($direction == only) {
    @media screen and (min-width: $lg) and (max-width: $xl - 1) {
      @content; } }
  @else if ($direction == up) {
    @media screen and (min-width: $lg) {
      @content; } }
  @else if ($direction == down) {
    @media screen and (max-width: $xl - 1) {
      @content; } } }

@mixin media-md($direction: only) {
  @if ($direction == only) {
    @media screen and (min-width: $md) and (max-width: $lg - 1) {
      @content; } }
  @else if ($direction == up) {
    @media screen and (min-width: $md) {
      @content; } }
  @else if ($direction == down) {
    @media screen and (max-width: $lg - 1) {
      @content; } } }

@mixin media-sm($direction: only) {
  @if ($direction == only) {
    @media screen and (min-width: $sm) and (max-width: $md - 1) {
      @content; } }
  @else if ($direction == up) {
    @media screen and (min-width: $sm) {
      @content; } }
  @else if ($direction == down) {
    @media screen and (max-width: $md - 1) {
      @content; } } }

@mixin media-xs($direction: only) {
  @if ($direction == up) {
    @media screen and (min-width: $xs) {
      @content; } }
  @else if ($direction == down or $direction == only) {
    @media screen and (max-width: $xs - 1) {
      @content; } } }
