@mixin align($justify-content: start, $align-items: stretch) {
  @if $justify-content == start or $justify-content == 0 {
    justify-content: flex-start; }
  @else if $justify-content == end {
    justify-content: flex-end; }
  @else if $justify-content == between {
    justify-content: space-between; }
  @else if $justify-content == around {
    justify-content: space-around; }
  @else if $justify-content == center {
    justify-content: center; }

  @if $align-items == stretch or $align-items == 0 {
    align-items: stretch; }
  @else if $align-items == start {
    align-items: flex-start; }
  @else if $align-items == end {
    align-items: flex-end; }
  @else if $align-items == baseline {
    align-items: baseline; }
  @else if $align-items == center {
    align-items: center; } }


