@mixin offset($direction: right, $val: auto) {
  @if $val == auto or $val == 0 {
    @if $direction == right {
      margin-left: auto; }
    @if $direction == left {
      margin-right: auto; } }

  @else if $val < $columns + 1 {
    @if $direction == right {
      margin-left: calc(100% / #{$columns} * #{$val} + #{$margin} / 2) !important; }

    @else if $direction == left {
      margin-right: calc(100% / #{$columns} * #{$val} + #{$margin} / 2); } } }

