.content {
  @include media-md(down) {
    width: 100%; }
  @include media-lg(up) {
    width: calc(100% - 240px - 48px); } }


// Related articles header
.related-articles {
  &_content {
    @include col(24); } }

.related-header {
  @include hidden-sm(down);
  margin-bottom: 40px;
  @include grid-in();
  @include align(end, center);
  &_xs {
    @include media-sm(down) {
      display: block;
      text-align: center;
      margin-bottom: 20px; } }
  &__title {
    @include media-md(down) {
      font-size: 32px; }
    @include media-lg(up) {
      font-size: 36px; }
    font-weight: 700;
    @include media-sm(down) {
      @include col(24); }
    @include col-md(10);
    @include offset-md(right, 6);
    @include offset-lg(8);
    @include col-lg(8);
    text-align: center;
    span {
      color: #00ad5d; } }
  &__listing-link {
    &.sm {
      text-align: center;
      margin: 20px;
      @include hidden-md(up); }
    &:not(.sm) {
      @include hidden-sm(down); }
    @include media-sm(down) {
      @include col(24); }
    @include col-md(8);
    font-size: 16px;
    font-weight: 700;
    color: #00ad5d;
    text-align: right;
    text-decoration: none;
    transition: color .3s ease;
    &:hover, &:focus {
      color: #076a3c; } } }

// Related articles list
.articles {
  @include grid-in();
  &_related {
    @include hidden-sm(down);
    @include align(between); } }


.article-preview {
  &_related {
    @include col-md(6);
    @include col-lg(5); }
  &_news {
    @include media-xl() {
      @include col(6); }
    @include media-lg() {
      @include col(8); }
    @include media-md() {
      @include col(8); }
    @include media-sm(down) {
      @include col(12); } }
  text-align: center;
  display: flex;
  flex-direction: column;
  @include align(between, center);
  padding-bottom: 50px;
  &__image {
    margin-bottom: 20px;
    overflow: hidden;
    display: block;
    width: 100%;
    & > img {
      max-width: 100%;
      min-width: 100%;
      transition: transform .6s ease;
      @include media-md(down) {
        width: 100%; } }
    &:hover, &:focus {
      & > img {
        transform: scale(1.05) rotate(2deg); } } }
  &__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px; }
  &__preview-text {
    font-size: 13px;
    font-weight: 300;
    color: #909090;
    margin-bottom: 20px; }
  & > .btn {
    margin-top: auto;
    text-transform: uppercase;
    font-size: 13px;
    border: 2px solid #c6c6c6;
    text-decoration: none;
    color: #171620;
    font-weight: 900;
    display: inline-block;
    padding: 10px 15px;
    @include media-lg(up) {
      width: 50%; }
    transition: border-color .3s ease;
    &:hover, &:focus {
      border-color: #909090; } } }

// Subscription
.subscription {
  display: flex;
  border-top: 50px solid #FFFFFF;
  background-color: #e8e8e8;
  padding-bottom: 60px;
  @include media-lg(up) {
    padding-bottom: 30px; }
  &__image {
    @include media-md(down) {
      display: none; }
    @include col(8);
    @include offset(right,1);
    & > img {
      position: relative;
      margin-top: -45px; } }
  &__form {
    @include col(22);
    @include offset(right,1);
    @include col-lg(13); }
  &__header {
    @include media-md(up) {
      display: flex; }
    margin-top: 80px;
    margin-bottom: 30px;
    @include media-sm(down) {
      text-align: center; } }
  &__percent-icon {
    padding: 20px;
    background-color: #fe7f39;
    color: #ffffff;
    font-size: 48px;
    @include media-lg(up) {
      font-size: 76px; }
    font-weight: 900;
    line-height: .8;
    @include media-md(up) {
      margin-right: (48/804) * 100%; }
    @include media-sm(down) {
      display: inline-block;
      margin-bottom: 20px; } }
  &__title {
    @include media-sm(down) {
      font-size: 25px; }
    @include media-md() {
      font-size: 30px; }
    @include media-lg(up) {
      font-size: 43px; }
    font-weight: 900;
    color: #292929;
    text-transform: uppercase; }
  &__text {
    @include hidden-sm(down);
    font-size: 16px;
    color: #646464;
    margin-bottom: 40px; }
  &__input-group {
    display: flex;
    & > button {
      flex-grow: 1; }
    @include media-sm(down) {
      flex-wrap: wrap; } }
  &__input {
    position: relative;
    @include media-sm(down) {
      width: 100%;
      padding: 14px;
      text-align: center;
      margin-bottom: 20px; }
    @include media-md(up) {
      flex-grow: 2;
      padding: 0 20px; }
    border: 1px solid transparent;
    transition: border-color .3s ease;
    &:focus {
      border-color: #00ad5d; } } }

// Listing header
.header-listing {
  @include col(24);
  display: flex;
  @include align(between, center);
  h1 {
    font-size: 22px;
    @include media-md(up) {
      font-size: 32px; }
    font-weight: 700;
    color: #272727;
    margin: 36px 0; }

  .goods-count {
    & > span {
      font-size: 14px;
      color: #999898;
      margin-right: 20px;
      @include hidden-sm(down); }
    & > i {
      font-size: 12px;
      color: #303030;
      text-align: center;
      line-height: 22px;
      font-weight: 700;
      font-style: normal;
      position: relative;
      top: -15px; } } }
// Look banner
.banner-look {
  @include hidden-md(down);
  @include col(24);
  margin-bottom: 24px;
  &__image {
    max-width: 100%;
    &_xl {
      @include hidden-md(down); }
    &_md {
      @include hidden-lg(up); } } }

// Filter show button
.btn_filter {
  @include hidden-lg(up);
  @include col(24);
  margin-bottom: 25px; }

// Sorting
.goods-sorting {
  @include media-sm(down) {
    flex-wrap: wrap; }
  @include col(24);
  @include col-lg(12);
  @include col-md(14);
  display: flex;
  @include align(start, center);
  & > * {
    vertical-align: middle; }
  & > span {
    font-size: 14px;
    font-weight: 700;
    color: #303030;
    margin-left: 10px;
    margin-right: 20px; } }
.sorting-select {
  width: 198px;
  padding: 8px 18px;
  border: 1px solid #B0C0CE;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  &.type-2 {
    line-height: 21px;
    background-color: #fff;
    margin-bottom: 1px; }
  &.day, &.month, &.year {
    display: inline-block; }
  &.day {
    width: 94px;
    width: 23%; }
  &.month {
    width: 144px;
    width: 42%; }
  &.year {
    width: 100px;
    width: 23%; }
  @include media-sm(down) {
    width: 100%;
    margin: 20px 0;
    padding: 11px 18px; }
  i {
    position: absolute;
    right: 10px;
    top: calc(50% - 3px);
    transition: transform .3s ease; }
  &__dropdown {
    width: 100%;
    overflow: hidden;
    max-height: 0;
    position: absolute;
    top: 34px;
    left: 0;
    background-color: #ffffff;
    z-index: 1;
    box-shadow: 0px 10px 24px 0px rgba(0, 0, 2, 0.09);
    transition: max-height .3s ease, padding .3s ease;
    &.scroll {
      overflow-y: scroll; } }
  &__option {
    padding: 10px 10px 10px 30px;
    transition: background-color .3s ease;
    &:not(:last-child) {
      border-bottom: 1px solid #ebebeb; }
    &:hover, &:focus {
      background-color: #f7f7f7; } }
  &.active {
    .sorting-select__dropdown {
      max-height: 240px; }
    i {
      transform: rotate(180deg); } } }

// Goods change view
.goods-view {
  @include col(12);
  display: flex;
  @include align(end, center);
  @include hidden-md(down);
  & > span {
    font-size: 14px;
    font-weight: 700;
    color: #303030;
    margin-right: 30px; }
  & > .icon-fade {
    cursor: pointer;
    transition: transform 1s ease;
    &.active {
      transform: rotateY(180deg); }
    &:last-child {
      margin-left: 20px; } } }
// Look link
.btn_look {
  @include hidden-sm(down);
  @include hidden-lg(up);
  @include col(8);
  @include offset(right, 2); }

// Goods listing
.goods-listing {
  margin-top: 30px;
  width: 100%;

  @include media-md(down) {
    @include grid-in();
    margin-left: 0;
    margin-right: 0;
    width: calc(100% + 24px); }
  @include media-lg(up) {
    @include grid();
    &.active {
      .product {
        @include col(12);
        display: flex;
        &__image {
          width: 50%;
          height: auto; }
        &__description {
          width: 50%;
          height: auto;
          padding-bottom: 0;
          border: 1px solid #EBEBEB;
          border-left: none; }
        &__hidden {
          border: none;
          position: relative;
          top: 0;
          left: 0;
          opacity: 1;
          height: auto;
          width: 100%; }
        &__wishlist {
          left: calc(50% - 57px); }
        &:hover, &:focus {
          .product__hidden {
            box-shadow: none; }
          .product__description {
            border-left: none; } } } } }
  & > .product {
    @include col(12);
    @include col-md(8);
    @include col-xl(6); } }

// Product tile
.product {
  text-align: center;
  margin-bottom: 24px;
  background-color: #fff;
  position: relative;
  transition: box-shadow .3s ease;
  @include media-md(down) {
    display: flex;
    flex-direction: column; }
  &__image {
    position: relative;
    border: 1px solid #ebebeb;
    //+media-xs()
    //  height: 201px
    //+media-sm(up)
    //  height: 342px
    display: block;
    & > img {
      display: block;
      max-width: 100%;
 } }      //+image-center()
  &__name {
    font-size: 14px;
    line-height: 16px;
    color: #272727;
    span {
      color: #a9a9a9; }
    @include media-sm(up) {
      padding: 0 10%; }
    @include media-lg(up) {
      height: 32px; }
    @include media-sm(down) {
      height: auto; }
    margin-bottom: 16px;
    display: block;
    text-decoration: none; }
  &__price {
    position: relative;
    @include media-md(down) {
      margin-top: auto; }
    @include media-md() {
      margin-bottom: 40px; }
    @include media-lg(up) {
      padding-bottom: 20px; }
    z-index: 1;
    background-color: #ffffff;
    .current {
      color: #000000;
      font-size: 20px;
      font-weight: 700; }
    .old-price {
      @include media-sm(down) {
        position: static;
        display: block; }
      padding-right: 24px;
      font-size: 13px;
      position: absolute;
      font-style: normal;
      line-height: 27px;
      margin-right: 10px;
      top: 24px;
      text-align: center;
      @include media-lg(up) {
        top: -11px;
        left: 50%;
        margin-left: -133px; }
      span {
        position: relative;
        &:after {
          content: "";
          display: block;
          position: absolute;
          top: 8px;
          left: -2px;
          width: 100%;
          height: 1px;
          background-color: #f24841; } } } }

  &__description {
    position: relative;
    padding-top: 25px;

    @include media-md(down) {
      height: auto;
      padding-bottom: 0;
      flex-grow: 1;
      display: flex;
      flex-direction: column; }

    @include media-lg(up) {
      padding-bottom: 46px; } }

  &__hidden {
    @include hidden-md(down);
    position: absolute;
    top: 100%;
    margin-top: -46px;
    left: -1px;
    width: calc(100% + 2px);
    opacity: 0;
    height: 0;
    background-color: #ffffff;
    border: 1px solid #EBEBEB;
    border-top: none;
    padding-bottom: 30px;
    transition: box-shadow .3s ease, opacity .3s ease; }
  &__size {
    margin: 25px 9% 0; }
  &__size-hidden {
    margin-bottom: 20px; }
  &__buy {
    font-weight: 700;
    width: 70%;
    & > * {
      & > * {
        vertical-align: middle; }
      & > i {
        margin-right: 10px; } }
    .done {
      display: none; }
    &.active {
      .done {
        display: inline-block;
        text-decoration: none;
        color: #ffffff; }
      .put {
        display: none; } } }
  &:hover, &:focus {
    @include media-md(up) {
      z-index: 2;
      box-shadow: 0px 0px 24px 0px rgba(0, 0, 2, 0.09); }
    @include media-sm(down) {
      z-index: 3; }
    .product__description {
      @include media-md(up) {
        border-left: 1px solid #EBEBEB;
        border-right: 1px solid #EBEBEB; } }
    .product__hidden {
      opacity: 1;
      height: auto;
      box-shadow: 0px 0 24px 0px rgba(0, 0, 2, 0.09); } }
  &__wishlist {
    @include hidden-xs(); } }

// Ribbons
.ribbons {
  position: absolute;
  top: 40px;
  left: 10px;
  @include media-xs() {
    top: 15px;
    left: -5px; }
  &__ribbon {
    display: block;
    width: 51px;
    height: 51px;
    border-radius: 50%;
    line-height: 53px;
    font-size: 14px;
    font-weight: 900;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center;
    margin-bottom: 10px;
    @include media-xs() {
      transform: scale(0.7); }
    &_hit {
      background-color: #ff7b41; }
    &_new {
      background-color: #36b057; }
    &_sale {
      color: #000000;
      background-color: #fcd82f; } } }

// WishList
.wishlist {
  width: 47px;
  height: 47px;
  box-shadow: 0px 0px 9px 0px rgba(0, 0, 2, 0.09);
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  background-color: #ffffff;
  transition: background-color .3s ease;
  .icon-fade {
    padding: 15px 10px 7px 11px;
    i:last-child {
      top: 15px;
      left: 11px; } }
  &:hover, &:focus {
    background-color: #F8F8F8; }
  .sprite_main-product__wishlist_done {
    margin: 14px 12px; }
  .done {
    display: none; }
  &.active {
    .popup-notice__text:not(.done), .icon-fade {
      display: none; }
    .done {
      display: inline-block; } } }

// Product rating
.product-rating {
  & > * {
    vertical-align: middle; }
  &__count {
    font-size: 12px;
    color: #000000;
    font-weight: 700;
    line-height: 28px;
    margin-right: 10px;
    text-align: center; }
  &__star {
    margin: 0 2px; } }

// Page navigation
.page-navigation {
  z-index: 1;
  padding: 10px 0 100px;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: flex-end;
  .btn_more {
    @include media-md(up) {
      text-align: right; }
    @include col(24);
    @include col-md(9);
    @include col-lg(8);
    @include col-xl(6); }
  .btn_show-all {
    @include col(24);
    @include media-sm(down) {
      margin-top: 20px; }
    @include media-md(up) {
      text-align: right; }
    @include col-md(8);
    @include col-lg(7);
    @include offset-lg(right, 2); }
  &.hidden {
    display: none; } }

// Full width content
.content-full-width {
  min-width: 100%;
  margin-top: 30px;
  z-index: 1;
  background-color: #ffffff; }
