.product-detailed {
  flex-grow: 1;
  padding-top: 30px;
  &__gallery {
    //background-color: yellow
    @include media-md(down) {
      margin-top: 30px;
      @include col(24); }
    @include col-sm(24);
    @include col-lg(12); }
  &__details {
    //background-color: yellowgreen
    @include col(24);
    @include col-lg(12);
    & > div {
      height: 100%; } }
  &__title {
    @include col(24);
    font-weight: 500;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 24px;
    &_lg-up {
      @include hidden-md(down); }
    &_md-down {
      @include hidden-lg(up); } }
  &__column {
    //background-color: cornflowerblue
    display: flex;
    @include media-lg(up) {
      flex-direction: column; }
    @include col-lg(12);
    @include media-md(down) {
      width: 100%;
      flex-wrap: wrap; }
    &:nth-child(3) {
      @include media-lg(up) {
        padding-left: 24px; } }
    & > *:first-child {
      @include media-lg(up) {
        margin-top: 0; } }
    & > .product-rating {
      @include media-xs() {
        @include col(24); }
      @include media-md(down) {
        @include col(12);
        margin-top: 30px;
        order: 1; } } }
  &__subtitle {
    font-size: 14px;
    color: #a9a9a9;
    margin-bottom: 15px;
    &_size {
      margin-top: 20px;
      @include media-md(down) {
        @include col(24);
        order: 2; } }
    &_des {
      @include media-md(down) {
        @include col(24);
        margin-top: 50px; } } }
  &__art {
    @include media-md(down) {
      @include col(24); }
    &_lg-up {
      @include hidden-md(down); }
    &_md-down {
      @include hidden-lg(up); } }

  &__price {
    @include media-xs() {
      @include col(24); }
    @include media-md(down) {
      @include col(12);
      margin-top: 30px; }
    @include media-lg(up) {
      margin-top: 20px; }
    & > * {
      vertical-align: middle; }
    &.product__price {
      .current {
        font-size: 30px; }
      .old-price {
        position: static;
        margin-left: 0;
        font-size: 16px;
        text-align: center;
        top: auto;
        left: auto;
        line-height: 33px;
        padding-right: 30px;
        &:after {
          top: 9px; } } } }

  &__size {
    padding-top: 0;
    .popup-notice_size {
      top: -132px; }
    @include media-sm(down) {
      @include col(24); }
    @include media-md(down) {
      @include col(12);
      order: 3; } }
  &__btn {
    width: 100%;
    @include media-xs() {
      @include col(24); }
    @include media-md() {
      @include col(12); }
    &_size {
      color: #272727;
      @include media-md(down) {
        order: 4;
        line-height: 34px; }
      font-size: 14px;
      padding: 10px;
      & > span {
        margin-left: 10px; } }
    &_buy {
      @include media-md(down) {
        order: 5;
        margin-top: 30px; }
      margin-top: 35px; }
    &_quick {
      @include media-md(down) {
        order: 6;
        margin-top: 30px; }
      margin-top: 20px;
      font-size: 18px; } }
  &__share {
    margin-top: auto;
    @include media-sm(down) {
      @include col(24); }
    @include media-md(down) {
      @include col(12);
      @include offset-md(right,6);
      order: 7;
      margin-top: 30px; } }
  &__description {
    margin-top: 20px;
    margin-left: -24px;
    border-bottom: 1px solid #dddddd;
    @include media-md(down) {
      @include col(24);
      border-left: none;
      padding-left: 0; } }
  &__delivery {
    @include media-md(down) {
      order: -1;
      @include col(24);
      border-left: none;
      border-top: 1px solid #dddddd;
      margin-top: 30px;
      padding: 20px 0;
      & > div {
        &:first-child {
          width: 260px;
          margin: 0 auto; } } } }
  &__warranty {
    @include media-sm(down) {
      flex-wrap: wrap; }
    @include media-md(down) {
      @include col(24);
      border-left: none;
      display: flex;
      @include align(between);
      padding-left: 0; } } }
.share {
  display: flex;
  @include align(between);
  &__separator {
    color: #c7c7c7; }
  &__link {
    min-width: 20px; } }
.product-gallery {
  @include media-sm(down) {
    width: 100%;
    flex-direction: column; }
  display: flex;
  &__navigation {
    @include media-sm(down) {
      display: flex;
      flex-wrap: wrap;
      @include align(between);
      order: 1;
      margin-top: -53px;
      z-index: 1; }
    @include media-md(up) {
      width: 97px;
      & > button {
        width: 100%; } }
    @include media-sm(down) {
      & > button {
        width: 52px;
        &:first-child {
          order: -2;
          margin-right: 12px;
          & > i {
            transform: rotate(-90deg); } }
        &:last-child {
          order: -1;
          margin-left: 12px;
          & > i {
            transform: rotate(90deg); } } } } }
  &__wrap {
    overflow: hidden;
    @include media-sm(down) {
      width: 100%; }
    @include media-md(up) {
      height: 509px; } }
  &__track {
    display: flex;
    @include media-md(up) {
      flex-direction: column; }
    &:not(.js-dragged) {
      transition: transform .3s ease; } }
  &__thumb {
    position: relative;
    width: 100%;
    height: 101.8px;
    border-bottom: 1px solid #dddddd;
    border-left: 1px solid #dddddd;
    overflow: hidden;
    @include media-sm(down) {
      border-top: 1px solid #dddddd;
      border-left: none;
      border-right: 1px solid #dddddd;
      &:last-child {
        border-left: 1px solid #dddddd; } }
    background-color: #ffffff;
    cursor: pointer;
    transition: box-shadow .3s ease;
    &:first-child {
      border-top: 1px solid #dddddd; }
    & > img {
      transform: translateZ(0);
      //+image-center()
      display: block;
      width: 100%; }
    &:hover, &:focus, &.active {
      box-shadow: 0 0 24px 0px rgba(0, 0, 0, 0.09);
      z-index: 1;
      &:before {
        background-color: #36b057;
        width: 6px; } }
    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 0;
      transition: background-color .3s ease, width .3s ease;
      z-index: 1; }

    &_video {
      position: relative;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      &__play {
        position: absolute;
        padding: 10px;
        background-color: #292929;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        &:before {
          content: "";
          display: block;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 14.5px 0 14.5px 30px;
          @include media-lg(up) {
            border-width: 10px 0 10px 20px; }
          border-color: transparent transparent transparent #ffffff;
          transform: rotate(360deg);
          transition: border-color .3s ease; } }
      &:hover {
        .product-gallery__thumb_video__play {
          &:before {
            border-color: transparent transparent transparent #fcd82f; } } } } }
  &__video {
    @include image-center();
    &:hover {
      & + .product-gallery__thumb_video__play {
        &:before {
          border-color: transparent transparent transparent #fcd82f; } } } }
  &__image-wrap {
    position: relative;
    display: block;
    @include media-sm(down) {
      height: 442px; }
    @include media-md(up) {
      height: 613px;
      width: calc(100% - 97px); }
    border: 1px solid #dddddd; }
  &__image {
    @include image-center(); }

  &__image-link {
    display: none;
    &.active {
      display: block; } } }
.description-scroll {
  height: 225px;
  padding: 0 0 40px 24px;
  border-left: 1px solid #dddddd;
  ::-webkit-scrollbar {
    width: 14px; }
  ::-webkit-scrollbar-track {
    border-radius: 10px;
    border-left: 6px solid #ffffff;
    border-right: 6px solid #ffffff;
    background-color: #dddddd; }
  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #ebebeb; }
  //background image in sprite_main css
  ::-webkit-scrollbar-button {
    background: url(data:image/pngbase64,) no-repeat;
    border: 3px solid #ffffff;
    width: 14px;
    height: 11px;
    &:start {
      background-position: -23px -5px; }
    &:end {
      background-position: -5px -5px; } }

  &__body {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    font-size: 14px;
    padding: 10px 20px 0 0;
    color: #666666; }

  &__link {
    display: block;
    font-size: 14px;
    color: #36b057; }
  &__param-title {
    & > span {
      margin-right: 10px; }
    &_strong {
      font-weight: 700;
      color: #000000; } }
  &__param-value {
    font-weight: 500;
    color: #666666; }
  &__mt {
    margin-top: 20px; }
  &__color-wrap {
    display: flex;
    @include align(between);
    & > * {
      display: flex;
      flex-grow: 1;
      &:first-child {
        margin-right: 15px; } } }
  &__color {
    display: inline-block;
    min-width: 18px;
    height: 18px;
    vertical-align: middle; } }

// Warranty
.product-warranty {
  border-left: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
  padding-left: 24px;
  @include media-md(up) {
    padding-top: 5px;
    padding-bottom: 30px; }
  @include media-sm(down) {
    padding-bottom: 20px; }
  margin-left: -24px;
  &__item {
    display: flex;
    @include media-md(up) {
      margin-top: 30px; }
    @include media-sm(down) {
      width: 100%;
      margin-top: 20px;
      &:first-child {
        border-bottom: 1px solid #dddddd;
        padding-bottom: 20px; } }
    & > i {
      margin-right: 28px;
      min-width: 26px;
      &.sprite_main-product_warranty-thumb-up {
        margin-right: 20px;
        min-width: 34px; } } }
  &__title {
    font-size: 13px;
    font-weight: 700;
    color: #36b057;
    margin-bottom: 5px; }
  &__note {
    font-size: 12px;
    color: #8c8c8c; } }

// Delivery
.product-delivery {
  color: #000000;
  border-left: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
  padding-left: 24px;
  padding-right: 24px;
  margin-left: -24px;
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
  transition: background-color .3s ease;
  position: relative;
  &__wrap {
    width: 100%;
    display: flex;
    @include align(between, center);
    & > i {
      &:first-child {
        margin-right: 20px; }
      &:last-child {
        margin-left: auto;
        transform: rotate(180deg);
        transition: transform .3s ease; } } }
  &:hover {
    background-color: #f8f8f8; }
  &.active {
    .product-delivery__wrap > i {
      transform: rotate(0deg); }
    .product-delivery__hidden {
      padding-top: 10px;
      padding-bottom: 20px;
      max-height: 400px;
      @include media-md(down) {
        border-top: 1px solid #dddddd; }
      @include media-lg(up) {
        border-bottom: 1px solid #dddddd; } } }
  &__title {
    font-size: 16px;
    font-weight: 700;
    margin-right: 10px; }
  &__note {
    font-size: 13px; }
  &__hidden {
    cursor: auto;
    @include media-lg(up) {
      left: -1px;
      top: 82px;
      border-left: 1px solid #dddddd;
      position: absolute;
      top: 100%;
      margin-top: 1px; }
    @include media-md(down) {
      min-width: 100%;
      margin-top: 20px;
      margin-bottom: -20px; }
    background-color: #ffffff;
    width: calc(100% + 1px);
    z-index: 2;
    padding: 0 24px 0 80px;
    @include media-xs() {
      padding-left: 60px; }
    font-size: 13px;
    color: #878787;
    max-height: 0;
    overflow: hidden;
    transition: padding .3s ease, max-height .3s ease;
    & > div {
      margin-top: 25px; } }
  &__link > a {
    font-weight: 700; }
  &__city {
    & > span {
      &:last-child {
        font-size: 14px;
        color: #000000;
        margin-left: 10px;
        & > i {
          transform: rotate(90deg) translateZ(0); } } } }
  &__store {
    margin-left: -80px;
    padding-top: 20px;
    padding-left: 32px;
    width: calc(100% + 80px + 24px);
    display: flex;
    @include align(start, center);
    border-top: 1px solid #dddddd;
    & > span {
      font-size: 14px;
      color: #000000;
      margin-left: 20px; } } }

// Product set
.product-set-title {
  margin-top: 30px;
  @include col(24);
  font-size: 20px;
  @include media-sm(down) {
    text-align: center;
    font-size: 16px; }
  span {
    &:first-child {
      color: #000000;
      @include media-md(up) {
        margin-right: 20px; }
      @include media-sm(down) {
        display: block; } }
    &:last-child {
      color: #969696; } } }

.product-set-banner {
  margin-top: 30px;
  @include col(24);
  @include col-lg(8);
  @include col-xl(6);
  height: 350px;
  background: url('/img/product-set-banner-min.jpg') top right no-repeat #fcd82f;
  @include media-xs(down) {
    background-position: bottom right -15px; }
  &__title {
    display: flex;
    @include align(start,center);
    margin-top: 50px;
    font-weight: 900;
    font-size: 22px;
    text-transform: uppercase;
    width: 100%;
    padding-right: 70px;
    span {
      margin-left: 20px; } }
  &__orange-cycle {
    padding: 20px;
    background-color: #fc942f;
    @include hidden-xs(); }
  &__text {
    @include media-sm(up) {
      padding: 30px 150px 0 35px; }
    @include media-xs(down) {
      padding: 30px 120px 0 20px; }
    font-size: 14px;
    color: #222222;
    line-height: 24px; } }

.product-set-goods {
  @include col(24);
  @include col-lg(16);
  @include col-xl(18); }

.products-carousel {
  position: relative;
  margin-top: 30px;
  display: flex;
  @include align(start,center);
  overflow: hidden;
  @include media-sm(down) {
    flex-wrap: wrap;
    @include align(between);
    margin-top: -45px; }
  &_full-width {
    @include media-lg(up) {
      margin-left: -47px;
      margin-right: -47px; }
    & > .products-carousel__wrap {
      overflow: visible;
      &:before, &:after {
        content: "";
        display: block;
        position: absolute;
        background-color: #fff;
        width: 100%;
        height: 100%;
        top: 0;
        z-index: 1; }
      &:before {
        @include media-md(up) {
          left: calc(32px - 100%); }
        @include media-sm(down) {
          left: 100%; } }
      &:after {
        @include media-md(up) {
          right: calc(32px - 100%); }
        @include media-sm(down) {
          right: 100%; } } } }
  & > button {
    height: 80px;
    position: relative;
    z-index: 2;
    &:first-child {
      & > i {
        transform: rotate(-90deg); } }
    &:last-child {
      & > i {
        transform: rotate(90deg); } }
    @include media-sm(down) {
      width: 52px;
      height: 52px;
      margin-bottom: 24px;
      &:first-child {
        order: -2;
        margin-right: 12px; }
      &:last-child {
        order: -1;
        margin-left: 12px; } } }
  &__wrap {
    min-width: calc(100% - 2*47px);
    overflow: hidden;
    @include media-sm(down) {
      width: 100%; } }
  &__track {
    @include grid-in();
    &:not(.js-dragged) {
      transition: transform .3s ease; } }
  &:hover {
    overflow: visible;
    z-index: 2; } }

// Product set item
.product-set-item {
  width: 177px;
  margin: 0 12px;
  text-align: center;
  position: relative;
  &__image {
    width: 100%;
    position: relative;
    border: 1px solid #ebebeb;
    //height: 180px
    display: block;
    overflow: hidden;
    & > img {
      //+image-center()
      width: 100%;
      display: block; } }
  &__name {
    max-width: 100%;
    display: block;
    min-height: 42px;
    font-size: 12px;
    line-height: 14px;
    color: #272727;
    margin-top: 5px;
    text-decoration: none; }
  &__price {
    height: 54px;
    margin-top: 5px;
    padding-bottom: 5px !important;
    &.product__price {
      & > span {
        display: block; }
      .old-price {
        position: static;
        margin-left: 0;
        margin-right: 0;
        padding-right: 0; } } }
  &__buy {
    span {
      &:last-child {
        display: none; } }
    &.active {
      span {
        &:first-child {
          display: none; }
        &:last-child {
          display: inline-block; } } } }
  &__description {
    padding-bottom: 1px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    position: relative; }
  &__size {
    width: 100%;
    margin-left: 0;
    position: absolute;
    height: 50%;
    padding-top: 24px;
    bottom: -1px;
    left: 0;
    background-color: rgba(255, 255, 255, .75);
    transform: translateY(100%);
    transition: transform .3s ease .3s;
    & > .size-filter__size {
      background-color: #fff;
      text-decoration: none;
      color: #272727;
      &.active {
        background-color: #fcd82f; }
      &.missing {
        color: gray;
        &.active {
          color: #fff;
          background-color: silver; } } }
    & > .popup-notice_size {
      top: -100%;
      width: 100%;
      left: 0; } }
  &:focus, &:hover {
    .product-set-item__size {
      transition: transform .3s ease;
      transform: translateY(0); } } }


// Reviews and vk comments
.product-reviews-and-comments {
  margin-top: 30px;
  @include col(24);
  @include col-lg(18);
  .wrapper {
    width: 100%; }
  &__tabs {
    display: flex;
    @include align(start, center);
    border-bottom: 1px solid #ebebeb; }
  &__tab {
    @include media-sm(up) {
      font-size: 20px; }
    @include media-xs(down) {
      font-size: 18px; }
    color: #000000;
    padding: 20px 0;
    margin-bottom: -1px;
    border-bottom: 5px solid transparent;
    cursor: pointer;
    transition: border-color .3s ease;
    & > span {
      font-size: 18px;
      color: #969696; }
    &:hover, &:focus, &.active {
      border-color: #fcd82f; } }
  &__separator {
    width: 1px;
    height: 36px;
    background-color: #d1d1d1;
    margin: 0 20px; }
  & > div {
    &:nth-child(2), &:nth-child(3) {
      display: none; }
    &.active {
      &:nth-child(2), &:nth-child(3) {
        display: flex; } } } }
// Review
.product-review {
  margin-top: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid #ebebeb;
  display: flex;
  flex-wrap: wrap;
  @include col(24);
  &__info {
    @include col(24);
    @include offset-md(right, 1);
    @include col-md(8);
    display: flex;
    & > i {
      margin-right: 20px; }
    & > div > .product-rating {
      margin-top: 15px;
      @include media-sm(down) {
        display: inline-block; } }
    @include media-sm(down) {
      margin-bottom: 20px; } }

  &__name {
    font-size: 16px;
    font-weight: 700;
    color: #000000; }
  &__date {
    margin-top: 10px;
    font-size: 12px;
    color: #a5a5a5;
    @include media-sm(down) {
      display: inline-block;
      margin-right: 10px; } }
  &__text {
    @include col(24);
    @include col-md(15);
    font-size: 14px;
    color: #000000; } }

// Reviews navigation
.product-reviews-navigation {
  margin-top: 30px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  @include media-md(down) {
    @include align(between); }
  & > .btn_more {
    @include col(24);
    @include col-md(9);
    @include offset-md(left, 2);
    @include col-xl(7);
    @include offset-lg(left, 2); }
  & > .btn_show-all {
    @include media-sm(down) {
      margin-top: 30px;
      @include col(24); } }
  &.hidden {
    display: none; } }

// Review form
.product-review-form {
  margin-top: 30px;
  @include col(24);
  @include col-lg(6);
  &__title {
    font-size: 20px;
    color: #000000;
    padding: 20px 0 23px; }
  &__body {
    padding: 30px;
    border: 1px solid #d1d1d1;
    & > textarea {
      height: 147px; }
    & > .rating-inputs {
      margin-top: 20px;
      display: flex;
      @include align(between);
      .label {
        display: block;
        text-align: center;
        margin-top: 10px;
        width: 22px; } } }
  &__label {
    font-size: 13px;
    color: #b5b5b5;
    &_mt {
      margin-top: 20px; } } }
.reviews {
  width: 100%; }
// Reviews empty
.reviews-empty {
  text-align: center;
  border-bottom: 1px solid #EBEBEB;
  padding: 100px 0 185px;
  @include col(24);
  &__title {
    margin-top: 30px;
    font-weight: 900;
    font-size: 18px;
    color: #000000; }
  &__text {
    display: inline-block;
    max-width: 510px;
    margin-top: 30px;
    font-size: 14px;
    color: #000000; }
  & > .product-rating {
    margin-top: 30px; } }

// Form success
.form-success {
  text-align: center;
  padding: 90px 0 115px;
  &__title {
    margin-top: 30px;
    font-size: 36px;
    font-weight: 900;
    color: #fcd82f; }
  &__text {
    margin-top: 30px;
    & > span {
      color: #36b057; } }
  &_modal {
    padding: 30px 40px 70px;
    & > .form-success__text {
      max-width: 420px; } } }

// Full width carousel seen products
.products-carousel-block {
  margin-top: 30px;
  &__title {
    text-align: center;
    @include media-md(up) {
      font-size: 24px; }
    @include media-sm(down) {
      font-size: 18px;
      margin-bottom: 10px; }
    color: #272727; } }
